import React from 'react';
import './About.css';

import Radium, {StyleRoot} from 'radium';
import { bounce } from 'react-animations';


function About() {

    const styles = {
        bounce: {
          animation: 'x 1s',
          animationName: Radium.keyframes(bounce, 'bounce'),
          loop: true
        }
      }


    return ( 

        
 
        <div className="about">
    
            <h1>Dear Reader,</h1>

            
            <h3 className="about__firstpara">Software is taking over the world, it is like a whole new world itself! Software is making a huge positive impact on humanity, as we can see that in the pandemic we are all facing right now. We are able to use things such as Google Meet and Zoom to continue education safely, communicate with our family and friends, etc. We are able to use things such as Netflix and Amazon Prime in order to watch new movies and tv shows on demand, safely from our homes. Overall, most of the things we are now doing involve some sort of software. This means, most of the things that we do, are all being done due to the work software engineers do on a daily basis.</h3>
            <h3 className="about__secondpara">It is clear that Software will continue to be a huge part of our lives, and software can solve the biggest problems in the world, only if we have the right minds in the field.</h3>
            <h3 className="about__thirdpara">Many of us, the future generation, are starting to find a passion in Software Engineering, and are going into the Computer Science field. The Computer Science field is very powerful and we can solve any problem with Computer Science! It is up to us to use our talent, work ethic, and passion in Computer Science, to solve the problems of today, in order to have a better tomorrow.</h3>
            <h3 className="about__fifthpara">Let's build Software in order to make the world a better place!</h3>
            <h3 className="about__conclusionpara">Satvik Kasinadhuni</h3>
        </div>
    );
}

export default About;